@import url(https://fonts.googleapis.com/css2?family=Shippori+Mincho:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

.App {
  min-height: 100vh;
  font-family: 'Noto Sans JP', sans-serif !important;
}


#stripes{
  z-index: 1;
  width:100%;
  height: 800px;
  overflow: hidden;
  transform-origin: 0;
  transform: skewY(-12deg);
  background: linear-gradient(150deg,#53f 15%,#05d5ff 70%,#a6ffcb 94%);

}

#stripes, #stripes span{
  position: absolute;
}

#stripes span{
  height: 40px;
}

#stripes :nth-child(1){
  width: 50%;
  left: 50%;
  top:40px;
  background:#4c29ff;
}

#stripes :nth-child(2){
  width: 14%;
  right: 0;
  bottom:40px;
  background: #2be7ff;
}

#stripes :nth-child(3){
  width: 25%;
  bottom: 0;
  right: 0;
  background: #a1ffc8;
}

@media (min-width:670px){
  #stripes span{
    height:150px;
  }
  #stripes :nth-child(1){
    top: 0;
  }
  #stripes :nth-child(2){
    width: 16.66667%;
    top: 300px;
    bottom: auto;
    background: #0dcfff;
  }
  #stripes :nth-child(3){
    width: 33.33333%;
    right: auto;
    background: #11bdff;
  }
}

@media (min-width: 880px){
  #stripes span{
    height: 190px;
  }

  #stripes :nth-child(1){
    width: 33.33333%;
    left: -16.66666%;
    background: #53f;
  }

  #stripes :nth-child(2){
    width: 33.33333%;
    top: 0;
    left: 16.66666%;
    right: auto;
    background: #4553ff;
  }

  #stripes :nth-child(3){
    width: 33.33333%;
    left: 49.99999%;
    bottom: auto;
    background: #4f40ff;
  }

  #stripes :nth-child(4){
    width: 33.33333%;
    top: 380px;
    right: -16.66666%;
    background: #25ddf5;
  }

  #stripes :nth-child(5){
    width: 33.33333%;
    bottom: 0;
    background: #1fa2ff;
  }
}

.content{
  position: absolute;
  width: calc(100vw - 100px);
  z-index: 2;
  padding: 50px;
}

body{
  margin: 0;
  padding: 0;
}

h1{
  font-family: 'Shippori Mincho', serif;
  margin: 100px auto;
  font-size: 46px;
  font-weight: 800;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: -1.7px;
}

h1, h2{
  color: white;
}

h2{
  font-size: 20px;
  margin: 10px 0;
}

button{
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 20px 80px;
  border-radius: 100px;
  outline: none;
  border: none;
  font-size: 20px;
  text-transform: uppercase;
  background: rgba(255,255,255,0.3);
  color: white;
  cursor: pointer;
  font-weight: bold;
  transition: 0.3s;
}

button:hover{
  background: rgba(255,255,255,0.4);
}

.call-to-action{
  display: grid;
  grid-auto-flow: column;
  place-content: center;
  margin: 100px 0;
  position: relative;
}

label{
  place-self: end start;
  font-size: 12px;
  position: absolute;
  top: 70px;
  left: calc(50% - 112px);
  color: white;
  opacity: 0.9;
}

.card-group{
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 50px;
}

.card{
  box-shadow: 0px 10px 20px rgba(100, 100, 100, 0.2);
  border-radius: 8px;
  padding: 25px;
  background: white;
  text-align: center;
  cursor: pointer;
  place-self: center;
  height: 200px;
  width: 400px;
  display: grid;
  place-items: center;
}


.container {
    position: relative;
    max-width: 1300px;
    margin: 120px auto 0;
    background: #fff;
    border-radius: 18px;
    padding: 10px;
    box-shadow: 0px 10px 20px rgba(100, 100, 100, 0.2);
}

.container.result{
    background: transparent;
    box-shadow: none;
    font-size: 20px;
}

.result h2{
    font-size: 26px;
}

.result h3{
    margin-top: 30px;
}

/* Result Component */
.result {
    padding: 0;
    color: white;
}

/* Quiz Component */
.answerOptions {
    margin: 0;
    padding: 0;
    list-style: none;
}

/* QuestionCount Component */
.questionCount {
    padding: 1.5rem 2.5rem 0;
    font-size: 14px;
}

/* Question Component */
.question {
    color: black;
    margin: 0;
    padding: 0.5rem 2.5rem 1.5rem 2.5rem;
}

/* AnswerOption Component */
.answerOption {
    border-top: 1px solid #eee;
}

.answerOption:hover {
    background-color: #eefbfe;
}

.radioCustomButton {
    position: absolute;
    width: auto;
    opacity: 0;
}

.radioCustomButton,
.radioCustomLabel {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
}

.radioCustomLabel {
    position: relative;
    width: 100%;
    margin: 0;
    padding: 1.5rem 2.5rem 1.5rem 5rem;
    font-size: 16px;
    line-height: 1;
    top: 0;
    left: 0;
    color: black;
}

.radioCustomButton ~ .radioCustomLabel:before {
    position: absolute;
    top: 20px;
    left: 38px;
    width: 28px;
    height: 28px;
    content: '';
    display: inline-block;
    vertical-align: middle;
    background: #fff;
    border: 1px solid #bbb;
    border-radius: 50%;
    transition: all 0.3s;
}

.radioCustomButton:checked ~ .radioCustomLabel:before {
    content: '';
    background: #8bc53f url(../../static/media/icon-check.689ef715.svg) no-repeat;
    background-size: 27px;
    border-color: #8bc53f;
}

/* Animation */
.fade-enter {
    opacity: 0;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 0.5s ease-in-out 0.3s;
}

.fade-leave {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 1;
}

.fade-leave.fade-leave-active {
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
}

.fade-appear {
    opacity: 0;
}

.fade-appear.fade-appear-active {
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
}

.result-end h4{
    font-size: 18px;
    margin: 30px 0 70px;
}

.result-end h3{
    font-size: 24px;
    margin: 30px 0 70px;
}

.result-end h2{
    font-size: 30px;
}

.squircles{
    display: grid;
    grid-auto-flow: column;
    justify-content: center;
    grid-gap: 50px;
    gap: 50px;
    align-items: center;
}

.squircle{
    display: grid;
    place-items: center;
    grid-gap: 10px;
    gap: 10px;
    position: relative;
    width: 200px;
    transition: 0.3s;
    cursor: pointer;
}

.squircle:hover {
    -webkit-filter: brightness(0.95);
            filter: brightness(0.95);
}

.squircle svg{
    position: absolute;
    top: 50px;
    left: 75px;
    color: white;
}

a{
    text-decoration: none;
}

.squircles .squircle span{
    font-size: 13px;
    color: black;
    font-weight: 700;
    text-shadow: 0px 0px 20px white, 0px 0px 10px white;
}

